import React from "react";
import { Link } from "react-router-dom";
import "./Card.css";
import "./Images.css";

function Card(props) {
  return (
    <div
      data-id={props.id}
      data-parentid={props.parentId}
      className={props.cName}
      title={props.name}
    >
      {!props.imagePath && (
        <span
          className="place-holder"
          data-id={props.id}
          data-parentid={props.parentId}
          onClick={props.renderChild}
        >
          {props.gender === "male" && (
            <i
              data-id={props.id}
              data-parentid={props.parentId}
              className="fas fa-male thumb"
            />
          )}
          {props.gender !== "male" && (
            <i
              data-id={props.id}
              data-parentid={props.parentId}
              className="fas fa-female thumb"
            />
          )}
        </span>
      )}
      {props.imagePath && props.imagePath !== "" && (
        <span
          className="thub-holder"
          data-id={props.id}
          data-parentid={props.parentId}
          onClick={props.renderChild}
        >
          <span
            data-id={props.id}
            className={`img-block img_` + props.imagePath}
          />
        </span>
      )}

      <Link
        title={props.name}
        className="name"
        to={{
          pathname: "/profile-detail/",
          search: "?id=" + props.id
        }}
      >
        {props.name}
      </Link>
    </div>
  );
}

export default Card;
